@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand";
  color: #333;
}

a {
  text-decoration: none;
}

h2 {
  font-size: 2rem;
}

@media screen and (min-width: 800px) {
  h2 {
    font-size: 2.5rem;
    line-height: 1;
  }

  body {
    font-size: 1rem;
  }
}

.heading {
  width: 90vw;  
  margin: 5rem auto 0 auto;
  max-width: 600px;
  padding-left: 2rem;
}

.instructions {
  width: 90vw;
  margin: 0 auto;
  max-width: 600px;
  padding-left: 2rem;

}

.section {
  width: 90vw;
  margin: 5rem auto;
  max-width: 600px;
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}
